import { DepositInfoProps, ResetInfoProps, TransferInfoProps, WithdrawInfoProps } from '../../../components';

export enum ModalType {
    transfer = 'transfer',
    deposit = 'deposit',
    withdraw = 'withdraw',
}

export type ModalTypeKeys = keyof typeof ModalType

export type ModalStatePlayLoad = {
    isShow: boolean,
}

export interface ModalState<T,I> {
    isShowTransfer: ModalStatePlayLoad & { props: TransferInfoProps<T, I> },
    isShowWithdraw: ModalStatePlayLoad & { props: WithdrawInfoProps<T, I> },
    isShowDeposit: ModalStatePlayLoad & { props: DepositInfoProps<T, I> },
    isShowResetAccount: ModalStatePlayLoad & { props: ResetInfoProps<T, I> },
}
