export const headerSortCell = css`
  cursor: pointer;
  display: flex;
`;
export const headerSortName = css`
  flex-grow: 1;
  overflow: hidden;
  text-overflow: ellipsis;
`;
export const StyledArrowSort = styled(Box)<BoxProps & { showUp:any,  showDown:any }>`
  margin-left: ${({theme}) => `${theme.unit / 2}px`};
  .up {
    width: 0;
    height: 0;
    border: ${({theme}) => `${theme.unit / 2}px`} solid transparent;
    border-bottom-color: ${({theme,showUp}) => showUp? `${theme.colorBase.textPrimary}` : `${theme.colorBase.textSecondary}`};
  }

  .down {
    width: 0;
    height: 0;
    border: ${({theme}) => `${theme.unit / 2}px`} solid transparent;
    border-top-color:  ${({theme,showDown}) => showDown? `${theme.colorBase.textPrimary}` : `${theme.colorBase.textSecondary}`};
    margin-top: ${({theme}) => `${theme.unit / 4}px`};
  }
` as React.ElementType<BoxProps & { showUp:any,  showDown:any }>


// type SharedHeaderCellProps<R, SR> = Pick<
//     HeaderRendererProps<R, SR>,
//     'sortDirection' | 'onSort' | 'priority'
//     >;
export interface SortableHeaderCellProps<R, SR=unknown> extends HeaderRendererProps<R, SR> {
    children?: React.ReactNode;
}

export function SortableHeaderCell<R, SR>({
                                                      column,
                                                      sortDirection,
                                                      priority,
                                                      onSort,
                                                      children
                                                  }: SortableHeaderCellProps<R, SR>) {
    // let sortText = '';
    // if (sortDirection === 'ASC') {
    //     sortText = '\u25B2';
    // } else if (sortDirection === 'DESC') {
    //     sortText = '\u25BC';
    // }
    if (column.sortable) {
        const showUp = sortDirection === 'ASC'
        const showDown =  sortDirection === 'DESC'

        return (
            <Box component={'span'} display={'flex'} alignItems={'center'} className={`rdg-header-sort-cell ${headerSortCell}`} onClick={(e:React.MouseEvent) => onSort(e.ctrlKey)}>
                  <span className={`rdg-header-sort-name ${headerSortName}`}>{children?children:column.name}</span>

                 <StyledArrowSort showUp={showUp} showDown={showDown} >
                     <div className="up"/>
                     <div className="down"/>
                 </StyledArrowSort>
                  {priority}
            </Box>

        );
    } else{
        return <>{children?children:column.name}</>;
    }


}

