import React from 'react'
import { Cell, PieChart, Pie, Legend, Tooltip, ResponsiveContainer } from "recharts";
import styled from '@emotion/styled'

const colors = [
    "#00BBA8",
    "#ED9526",
    "#1C60FF",
    "#FFCA28",
    "#63992D",
    "#FF5677",
];

const StyledLegendItem = styled.div`
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 120px;
        margin-bottom: 8px;
        color: ${({theme}) => `${theme.colorBase.textChartLegend}`};
        font-size: 14px;

        &>span:first-of-type {
            display: flex;

            &>div:first-of-type {
                margin-right: 4px;
            }
        }
    `

export interface DoughnutChartProps {
    data?: {
        name: string;
        value: number;
    }[]
}

export const DoughnutChart = (props: DoughnutChartProps) => {
    const { data = [] } = props
    const getFormattedData = React.useCallback(() => {
        if (!data.length) return []
        if (data.length < 7) return data
        data.sort((a, b) => b.value - a.value)
        const others = data.slice(5, data.length).reduce((a, b) => ({
            name: 'Others',
            value: a.value + b.value
        }))
        const result = data.slice(0, 5).concat([others])
        return result
    }, [data])

    const renderLegend = (props: any) => {
        const { payload } = props;
        
        return (
            <ul style={{ marginRight: 26 }}>
                {
                    payload.map((entry: any, index: number) => {
                        const { color, value, payload: { value: amount } } = entry
                        const StyledColoredRect = styled.div`
                            width: 12px;
                            height: 12px;
                            background-color: ${color}
                        `
                        return (
                            <li key={`item-${index}`}>
                                <StyledLegendItem>
                                    <span><StyledColoredRect />{value}</span>
                                    <span>${amount}</span>
                                </StyledLegendItem>
                            </li>
                        )
                    })
                }
            </ul>
            );
        }

    return <>
            <ResponsiveContainer debounce={1} width={'99%'}>
                <PieChart>
                    <Pie data={getFormattedData()} dataKey={'value'} outerRadius={55} innerRadius={45}>
                        {getFormattedData().map((entry, index) => (
                            <Cell key={entry.name} fill={colors[index]} />
                        ))}
                    </Pie>
                    <Tooltip />
                    
                    <Legend layout="vertical" align="right" verticalAlign="middle" content={renderLegend} />
                </PieChart>
            </ResponsiveContainer>
        </>
}

