import React from 'react'
import { Box } from '@material-ui/core'
import styled from '@emotion/styled'
import { WithTranslation, withTranslation, TFunction } from 'react-i18next'
import { cloneDeep } from 'lodash'
import moment from 'moment'
import { Table, generateColumns, generateRows, Column } from '../../basic-lib/tables'
import { TablePagination } from '../../basic-lib'
import { Filter, FilterTradeTypes } from './components/Filter'

interface Row {
    side: TradeTypes;
    amount: {
        from: {
            key: string;
            value: number;
        },
        to: {
            key: string;
            value: number
        }
    };
    price: number;
    fee: number;
    time: number;
    cellExpend?: {
        value: string
        children: []
        isExpanded: boolean
    };
    children?: Row[];
    isExpanded?: boolean;
    formatter?: any;
}

export enum TradeTypes {
    buy = 'Buy',
    sell = 'Sell'
}

export type RawDataTradeItem = {
    side: TradeTypes;
    amount: {
        from: {
            key: string;
            value: number;
        },
        to: {
            key: string;
            value: number
        }
    };
    price: {
        key: string;
        value: number;
    };
    fee: {
        key: string;
        value: number;
    };
    time: number;
}

export type TradeTableProps = {
    rawData: RawDataTradeItem[];
    pagination?: {
        pageSize: number
    }
    showFilter?: boolean
}

enum ActionType {
    filter = 'filter',
    page = 'page'
}

const StyledSideCell: any = styled(Box)`
    color: ${(props: any) => {
        const {
            value,
            theme: {colorBase},
        } = props
        return value === TradeTypes.buy ? colorBase.success : colorBase.error
    }}
`

const getColumnModeAssets = (t: TFunction<"translation">): Column<Row, unknown>[] => [
    {
        key: 'side',
        name: t('labelTradeSide'),
        formatter: ({row, column}) => {
            const tradeType = t(row[column.key])
            
            return (
                <div className="rdg-cell-value">
                    <StyledSideCell value={tradeType}>
                        {tradeType}
                    </StyledSideCell>
                </div>
            )
        }
    },
    {
        key: 'amount',
        name: t('labelTradeAmount'),
        formatter: ({row, column}) => {
            const {from, to} = row[column.key]
            return (
                <div className="rdg-cell-value">
                    {`${from.value} ${from.key} -> ${to.value} ${to.key}`}
                </div>
            )
        }
    },
    {
        key: 'price',
        name: t('labelTradePrice'),
        formatter: ({row, column}) => {
            const {key, value} = row[column.key]
            return (
                <div className="rdg-cell-value">
                    {`${value} ${key}`}
                </div>
            )
        }
    },
    {
        key: 'fee',
        name: t('labelTradeFee'),
        formatter: ({row, column}) => {
            const {key, value} = row[column.key]
            return (
                <div className="rdg-cell-value">
                    {`${value} ${key}`}
                </div>
            )
        }
    },
    {
        key: 'time',
        name: t('labelTradeTime'),
        // minWidth: 400,
        formatter: ({ row, column }) => {
            const time = row[column.key]
            return (
                <div className="rdg-cell-value">
                    {`${time} ${t('days')}`}
                </div>
            )
        }
    },
]

export const TradeTable = withTranslation('tables')((props: WithTranslation & TradeTableProps) => {
    const { t, pagination, showFilter } = props
    
    const formattedRawData = props.rawData.map(o => Object.values(o))
    const [filterType, setFilterType] = React.useState(FilterTradeTypes.allTypes)
    const [filterDate, setFilterDate] = React.useState(null)
    const [page, setPage] = React.useState(1)
    const [totalData, setTotalData] = React.useState(formattedRawData)

    const defaultArgs: any = {
        rawData: [],
        columnMode: getColumnModeAssets(t).filter(o => !o.hidden),
        generateRows,
        generateColumns,
        style: {
            backgroundColor: ({colorBase}: any) => `${colorBase.backgroundBox}`
        }
    }

    const pageSize = pagination ? pagination.pageSize : 10;

    const getRenderData = React.useCallback(() => pagination
            ? totalData.slice((page - 1) * pageSize, page * pageSize)
            : totalData
    , [page, pageSize, pagination, totalData])

    const updateData = React.useCallback(({ 
        actionType,
        currFilterType = filterType,
        currFilterDate = filterDate,
    }) => {
        let resultData = cloneDeep(formattedRawData)
        // o[0]: tradeType
        if (currFilterType !== FilterTradeTypes.allTypes) {
            resultData = resultData.filter(o => o[ 0 ] === currFilterType)
        }
        if (currFilterDate) {
            const diff = moment(moment()).diff(currFilterDate, 'days')
            // o[4]: date
            resultData = resultData.filter(o => o[ 4 ] === diff)
        }
        if (actionType === 'filter') {
            setPage(1)
        }
        setTotalData(resultData)
    }, [formattedRawData, filterDate, filterType])

    const handleFilterChange = React.useCallback(({ filterType, filterDate }) => {
        setFilterType(filterType)
        setFilterDate(filterDate)
        updateData({ actionType: ActionType.filter, currFilterType: filterType, currFilterDate: filterDate })
    }, [updateData])

    const handlePageChange = React.useCallback((page: number) => {
        setPage(page)
        updateData({ actionType: ActionType.page, currPage: page })
    }, [updateData])

    return <>
        {showFilter && (
            <Filter handleFilterChange={handleFilterChange} />
        )}
        <Table {...{...defaultArgs, ...props, rawData: getRenderData() }}/>
        {pagination && (
            <TablePagination page={page} pageSize={pageSize} total={totalData.length} onPageChange={handlePageChange}/>
        )}
    </>
})
