import { createSlice, PayloadAction } from '@reduxjs/toolkit'
// import { ColorLib, LangType } from 'defs/common_defs'
import { ModalState, ModalStatePlayLoad } from './interface';
import {
    DepositInfoProps,
    ResetInfoProps,
    TransferInfoProps,
    WithdrawInfoProps
} from '../../../index';
import { IBData, WithdrawType, WithdrawTypes } from 'static-resource';


let deposit: DepositInfoProps<IBData<any>, any> = {
    tradeData:{belong:undefined} as any,
    coinMap:{},
    walletMap:{},
    depositBtnStatus: 'AVAILABLE',
}

let resetProps: ResetInfoProps<IBData<any>, any> = {
    tradeData:{belong:undefined} as any,
    coinMap:{},
    walletMap:{},
    resetBtnStatus: 'AVAILABLE',
    fee: {count: 0, price: 0 }
}

let withdrawProps: WithdrawInfoProps<IBData<any>, any> = {
    tradeData:{belong:undefined} as any,
    coinMap:{},
    walletMap:{},
    withdrawBtnStatus: 'AVAILABLE',
    withdrawType: WithdrawType.Fast,
    withdrawTypes: WithdrawTypes,
    chargeFeeToken: '',
    chargeFeeTokenList: [],

}

let transferProps: TransferInfoProps<IBData<any>, any> = {
    tradeData:{belong:undefined} as any,
    coinMap:{},
    walletMap:{},
    transferBtnStatus: 'AVAILABLE',
    chargeFeeToken: '',
    chargeFeeTokenList: [],

}


const initialState: ModalState<IBData<any>,any> = {
    isShowTransfer: {isShow:false,props:transferProps},
    isShowWithdraw:{isShow:false,props:withdrawProps},
    isShowDeposit: {isShow:false,props:deposit},
    isShowResetAccount: {isShow:false,props:resetProps},
}

export const modalsSlice = createSlice({
    name: 'settings',
    initialState,
    reducers: {
        setShowTransfer(state, action: PayloadAction<ModalStatePlayLoad & {props?:Partial<TransferInfoProps<IBData<any>,any>>} >) {
            const {props,isShow} = action.payload;
            state.isShowTransfer.isShow = isShow;
            if(props) {
                Object.keys(props).map(key=>{
                    state.isShowTransfer.props[key] = props[key]
                })
            }
        },
        setShowWithdraw(state, action: PayloadAction<ModalStatePlayLoad & {props?:Partial<WithdrawInfoProps<IBData<any>,any>>}>) {
            const {props,isShow} = action.payload;
            state.isShowWithdraw.isShow = isShow;
            if(props) {
                Object.keys(props).map(key=>{
                    state.isShowWithdraw.props[key] = props[key]
                })
            }
        },
        setShowDeposit(state, action: PayloadAction<ModalStatePlayLoad & {props?:Partial<DepositInfoProps<IBData<any>,any>>}>) {
            const {props,isShow} = action.payload;
            state.isShowDeposit.isShow = isShow;
            if(props) {
                Object.keys(props).map(key=>{
                    state.isShowDeposit.props[key] = props[key]
                })
            }
        },
        setShowResetAccount(state, action: PayloadAction<ModalStatePlayLoad & {props?:Partial<ResetInfoProps<IBData<any>,any>>}>) {
            const {props,isShow} = action.payload;
            state.isShowResetAccount.isShow = isShow;
            if(props) {
                Object.keys(props).map(key=>{
                    state.isShowResetAccount.props[key] = props[key]
                })
            }
        },
    },
})
export const {setShowTransfer, setShowWithdraw, setShowDeposit, setShowResetAccount} = modalsSlice.actions
