import { PanelContent,} from '../../basic-lib';
import { AmmChgData, AmmWithdrawWrap, IconButtonStyled } from '../components';
import { BoxProps, Grid, Tab, Tabs, Toolbar } from '@material-ui/core';
import { AmmInData, IBData, RefreshIcon } from '../../../static-resource';
import { AmmDepositWrap } from '../components/panel/AmmWrap/AmmDeposit';
import { WithTranslation, withTranslation } from 'react-i18next';
import { AmmPanelProps, AmmPanelType } from './Interface';
import React from 'react';
import { useDeepCompareEffect } from 'react-use';
import { Box } from '@material-ui/core/';
import { AmmData } from '../Interface';
import SwipeableViews from 'react-swipeable-views';
import { useTheme } from '@emotion/react';
import styled from '@emotion/styled';

const WrapStyled = styled(Box)<BoxProps & {height:any,width:any}>`
  ${({height, width, theme}) => `       
    background-color: ${theme.colorBase.background().default};
    width: ${typeof width === 'string' ? width : typeof width === 'number' ? width + 'px' : `var(--swap-box-width)`}; 
    ${theme.border.defaultFrame({c_key: 'blur'})};
    .container{
       padding:0 0 ${theme.unit * 3 + 'px'};
       min-height: var(--swap-box-height);
       height:100%;
       // height: ${typeof height === 'string' ? height : typeof height === 'number' ? height + 'px' : `var(--swap-box-height)`};
       // width: 100%;
    }
    .react-swipeable-view-container{
          height: ${typeof height === 'string' ? height : typeof height === 'number' ? `calc(${height}px - var(--toolbar-row-height))` : `calc(var(--swap-box-height) - var(--toolbar-row-height))`};
    }
     .MuiToolbar-root {
        align-content: stretch;
        justify-content: flex-end;
        padding:0 ${theme.unit / 2 * 5 + 'px'} ${theme.unit / 2 + 'px'};    
        box-sizing: border-box;
        height: var(--toolbar-row-height);
        min-height:  var(--toolbar-row-height);
        max-height:  var(--toolbar-row-height);
        .MuiIconButton-root {
            height: var(--btn-icon-size);
            width: var(--btn-icon-size);
            min-width: var(--btn-icon-size);
            margin: 0;
            display: flex;
            padding: 0;
            justify-content: center;
            justify-items: center;
            align-items: center
            font-size: ${theme.fontDefault.h4}; 
        }
        // .go-back{
        //   font-size: ${theme.fontDefault.h4}; 
        //   margin-right: ${theme.unit + 'px'}; 
        //   transform: rotate(90deg);
        // }
    }
     `};
  
` as React.ElementType<BoxProps & {height:any,width:any}>;

enum AmmPanelTypeMap {
    Deposit = 0,
    WithDraw = 1
}
const TabPanelBtn = ({t,value,handleChange}:WithTranslation & any) => {
      console.log(value)
    return <Tabs
        value={value}
        onChange={handleChange}
        aria-label="disabled tabs example"
    >
        <Tab label={t('labelLiquidityDeposit')} value={0}/>
        <Tab label={t('labelLiquidityWithdraw')}  value={1}/>
    </Tabs>
}
export const AmmPanel = withTranslation('common', {withRef: true})(<T extends AmmData<C extends IBData<I>?C:IBData<I>>,I,
    ACD extends AmmInData<I>,
    C = IBData<I>>(
    {
        t,
        tabSelected= AmmPanelType.Deposit,
        ammData,
        disableDeposit,
        disableWithdraw,
        handleDepositEvent,
        handleWithDrawEvent,
        ammCalcData,
        tokenDepositAProps,
        tokenDepositBProps,
        tokenWithDrawAProps,
        tokenWithDrawBProps,
        ammDepositBtnStatus,
        ammWithdrawBtnStatus,
        onAmmDepositClick,
        onAmmWithdrawClick,
        onChangeEvent,
        handleError,
        height, width,
        ...rest
    }: AmmPanelProps<T, I, ACD,C> & WithTranslation) => {
    const [index, setIndex] = React.useState( AmmPanelTypeMap[tabSelected]);
    const [ammChgData,setAmmChgData] =  React.useState({tradeData: ammData,type:'coinA'})
    useDeepCompareEffect(() => {
        if (ammData !== ammChgData.tradeData) {
            setAmmChgData({...ammChgData, tradeData: ammData});
        }
        // if( AmmPanelTypeMap[tabSelected] !==  index) {
        //     setIndex(AmmPanelTypeMap[tabSelected])
        // }
    }, [ammData])
    const _onChangeEvent = React.useCallback(async ( {tradeData,type}:AmmChgData<T>) => {
        await handleDepositEvent(tradeData,type);
        if (typeof onChangeEvent == 'function') {
            setAmmChgData(onChangeEvent({tradeData, type}));
        } else {
            if(index === 0) {
                handleWithDrawEvent(tradeData,type)
            }else{
                handleWithDrawEvent(tradeData,type)
            }
        }

    }, [handleWithDrawEvent, handleWithDrawEvent,onChangeEvent, index, setAmmChgData]);
    // const [tab, setTab] = React.useState(0);
    const handleTabChange = React.useCallback((_event: any, newValue: any) => {
        if(index!== newValue){
            setIndex(newValue);
        }

    },[index]);
    const panelList: Pick<PanelContent<"ammDeposit"|"ammWithdraw">,'key'|'element'>[]= [
        {
            key: "ammDeposit",
            element: () => <AmmDepositWrap<T, I, ACD, C> key={"ammDeposit"} {...{
                t,
                ...rest,
                disableDeposit,
                ammDepositBtnStatus,
                ammCalcData,
                onChangeEvent:_onChangeEvent,
                onAmmDepositClick,
                handleError,
                ammData,
                tokenAProps: {...tokenDepositAProps},
                tokenBProps: {...tokenDepositBProps},
            }}/>,
        },
        {
            key: "ammWithdraw",
            element: () => <AmmWithdrawWrap<T, I, ACD, C> key={"ammWithdraw"} {...{
                t,
                ...rest,
                disableDeposit,
                ammWithdrawBtnStatus,
                ammCalcData,
                onChangeEvent:_onChangeEvent,
                onAmmWithdrawClick,
                handleError,
                ammData,
                tokenAProps: {...tokenWithDrawAProps},
                tokenBProps: {...tokenWithDrawBProps},
            }}/>,
        },
    ];
    const  theme = useTheme();
    return <WrapStyled height={height} width={width}>
        <Grid container className={'container'} direction={'column'}
              justifyContent={"start"} flexWrap={'nowrap'}>
            <Toolbar variant={'dense'}>
                <Box display={'flex'} flexDirection={'row'} alignItems={'center'}>
                    <Box paddingRight={3}>
                        <TabPanelBtn {...{t,value:index,handleChange:handleTabChange,...rest}} />
                    </Box>
                    <Box alignSelf={'flex-end'}>
                        <IconButtonStyled edge="end"
                                          className={'switch outline'}
                                          color="inherit"
                                          aria-label="to Professional" >
                            <RefreshIcon/>
                        </IconButtonStyled>
                    </Box>
                </Box>
            </Toolbar>
            {/*<Grid item flex={1} justifyContent={'stretch'} alignItems={'stretch'} height={'100%'}>*/}
                <SwipeableViews axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'} index={index}>
                    {panelList.map((panel) => {
                        return <Grid item justifyContent={'space-evenly'} alignItems={'stretch'} height={'100%'} key={panel.key}>{panel.element()}</Grid>
                    })}
                </SwipeableViews>
            {/*</Grid>*/}
        </Grid>
    </WrapStyled>
})


